/* latin */
@font-face {
    font-family: 'Damion';
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: local('Damion'), url(https://img1.wsimg.com/gfonts/s/damion/v10/hv-XlzJ3KEUe_YZkamw2EzJwVw.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.btn {
    background-color: #40BFFF;
    padding: 16px;
    color: #fff;
    font-weight: 700;
    font-size: 14px;
    width: 100%;
    border: 0;
    border-radius: 5px;
    outline: none;
    text-align: center;
}

.btn.small {
    width: fit-content;
    margin: auto;
    padding: 16px 30px;
}

.btn.portfolio {
    margin: 15px 0 30px;
}

input[type=text],
textarea,
.loginbtn {
    width: 100%;
    background: #FFFFFF;
    border: 1px solid #EBF0FF;
    border-radius: 5px;
    color: #9098B1;
    font-weight: 400;
    font-size: 14px;
    margin-bottom: 15px;
    padding: 15px;
    outline: none;
}

input[type=text]:focus,
textarea:focus {
    border: 1px solid #40BFFF;
}

.or {
    position: relative;
    font-size: 14px;
    font-weight: 700;
    text-align: center;
    margin: 30px 0;
}

.or span {
    position: relative;
    z-index: 1;
    padding: 0 10px;
    background-color: #fff;
}

.or::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    height: 1px;
    background-color: #EBF0FF;
}

.loginbtn {
    font-weight: 700;
    text-align: center;
}

.loginblue {
    text-align: center;
    font-size: 12px;
    font-weight: 700;
    color: #40BFFF;
}

.bold1 {
    color: #223263;
    font-weight: 700;
    font-size: 15px;
    letter-spacing: 0.5px;
    margin: 25px 0;
}

.bold2 {
    color: #223263;
    font-weight: 700;
    font-size: 18px;
    letter-spacing: 0.5px;
    margin: 15px 0;
}

.bold3 {
    color: #223263;
    font-weight: 700;
    font-size: 48px;
    letter-spacing: 0.5px;
    margin: 15px 0 50px;
}

.footer {
    position: fixed;
    width: 100%;
    bottom: 0;
    left: 0;
    padding: 25px 0;
    font-weight: 400;
    font-size: 12px;
    color: #9098B1;
    display: flex;
    justify-content: space-around;
    border-top: 1px solid #EBF0FF;
    background-color: #fff;
    z-index: 1000;
}

.footer .active {
    font-weight: 700;
    color: #40BFFF;
}

.plus {
    position: relative;
    width: 150px;
    height: 150px;
    background-color: #40BFFF;
    border-radius: 50%;
    margin: 50px auto 20px;
    box-shadow: 0 10px 15px rgba(0,0,0,0.2);
}

.plus::before,
.plus::after {
    content: '';
    position: absolute;
    width: 75%;
    height: 15px;
    background-color: #9098B1;
    border-radius: 10px;
}

.plus::before {
    top: 50%;
    left: 12.5%;
    transform: translateY(-50%);
}

.plus::after {
    top: 45%;
    left: 50%;
    transform: translateX(-50%) rotate(90deg);
}

.user {
    text-align: center;
    margin-bottom: 50px;
}

.add-photo {
    width: 145px;
    height: 145px;
    border: 1px solid #EBF0FF;
    border-radius: 32px;
    margin: 15px auto;
    color: #9098B1;
    font-weight: 700;
    letter-spacing: 0.5px;
    text-align: center;
    font-size: 12px;
}

.add-photo span {
    display: block;
    font-size: 100px;
    font-weight: 100;
}

.add-photo img {
    height: 100%;
    width: 100%;
}

.card {
    display: flex;
    padding: 15px;
    align-items: center;
    border: 1px solid #EBF0FF;
    border-radius: 5px;
    font-weight: 700;
    letter-spacing: 0.5px;
    color: #40BFFF;
    font-size: 12px;
    margin: 15px 0;
}

.card img {
    width: 72px;
    height: 72px;
    margin-right: 15px;
}

.card>div {
    line-height: 1.5;
}

.card>div>div:first-child {
    font-size: 19px;
    color: #223263;
}

.card>div>div:last-child {
    color: #53D1B6;
}

.sharediv {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
    width: 90%;
    margin: 15px auto 30px;
    border: 2px solid rgba(0, 0, 0, 0.16);
    font-size: 16px;
    font-weight: 700;
    color: #000000;
}

.sharediv:not(:last-of-type) img{
    margin-right: -20px;
}